<template>
	<div class="box">
		<layout-box>
			<box-item title="品牌总览" :loading="loading">
				<div class="overview">
					<div class="head">
						<div class="count">{{ totalBrandCount }}</div>
						<div class="total">品牌总数</div>
					</div>
					<div class="body">
						<div class="classify-box">
							<div class="classify" v-for="item in categoryData" :key="item.businessCategoryId">
								<div>
									<div class="icon">
										<CircleProgress :percentage="item.rate" color="#00F6FF">
											<span class="icon-text">{{ item.rate }}%</span>
										</CircleProgress>
									</div>
									<div class="desc">
										<div class="name">{{ item.businessCategoryNickname }}</div>
										<div class="number">{{ item.count }}个</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</box-item>
		</layout-box>
	</div>
</template>
<script>
import LayoutBox from '@/components/common/LayoutBox.vue';
import CircleProgress from '@/components/common/circle-progress.vue';
import BoxItem from '@/components/common/BoxItem.vue';
import {areaLevelCorAreaId} from '@/common/fn';
export default {
	props: {
		farmAreaInfo: {
			type: Object,
			default: () => areaLevelCorAreaId()
		}
	},
	data() {
		return {
			overviewData: [
				{
					id: 0,
					name: '蔬菜食用菌',
					amount: 45
				},
				{
					id: 1,
					name: '粮食',
					amount: 29
				},
				{
					id: 2,
					name: '瓜果',
					amount: 60
				},
				{
					id: 3,
					name: '禽畜',
					amount: 13
				},
				{
					id: 4,
					name: '水产',
					amount: 10
				},
				{
					id: 5,
					name: '其他',
					amount: 20
				}
			],
			categoryData: [],
			totalBrandCount: 0,
			loading: false
		};
	},
	async mounted() {
		this.getBrandStatics();
		this.getBrandCategory();
	},
	methods: {
		// 品牌统计
		getBrandStatics() {
			this.$ajax
				.post(this.$api.brand.getPpBrandStatics, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(res => {
					if (res.data.respCode === 0) {
						this.totalBrandCount = res.data.obj.totalCount;
					}
				});
		},
		// 获取分类品牌
		getBrandCategory() {
			this.loading = true;
			this.$ajax
				.post(this.$api.brand.getPpBrandCategory, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(res => {
					if (res.data.respCode === 0) {
						this.categoryData = res.data.obj;
					}
					this.loading = false;
				});
		}
	},
	components: {
		LayoutBox,
		BoxItem,
		CircleProgress
	}
};
</script>
<style scoped lang="scss">
.box {
	box-sizing: border-box;
	padding: 10px;
	height: 50%;
}
.overview {
	height: 100%;
	.head {
		padding: 5% 0;
		margin: 0 25px;
		border-bottom: 1px solid #07758d;
		.count {
			text-align: center;
			font-size: 48px;
			line-height: 20px;
			letter-spacing: 2.5px;
			font-weight: bold;
			color: #feeb48;
			margin-bottom: 16px;
		}
		.total {
			text-align: center;
			color: #008092;
			font-size: 16px;
			line-height: 20px;
		}
	}
	.body {
		display: flex;
		justify-content: center;
		align-items: center;
		height: calc(100% - 97px);
		box-sizing: border-box;
		padding: 0 42px;
		.classify-box {
			display: flex;
			flex-wrap: wrap;
		}
		.classify {
			width: 50%;
			display: flex;
			&:nth-of-type(2n) {
				justify-content: flex-end;
			}
			margin: 2% 0px;
			& > div {
				display: flex;
				align-items: center;
			}

			.icon {
				width: 60px;
				height: 60px;
				.icon-text {
					font-size: 12px;
					color: #00f6ff;
				}
			}
			.desc {
				margin-left: 18px;
				.name {
					color: #9dd1d7;
					font-size: 16px;
					line-height: 30px;
				}
				.number {
					position: relative;
					color: #00f6ff;
					font-size: 14px;
					line-height: 21px;
					padding-left: 14px;
					&::before {
						content: '';
						position: absolute;
						left: 0;
						top: 50%;
						transform: translateY(-50%);
						width: 8px;
						height: 8px;
						background-color: #00f6ff;
						border-radius: 50%;
					}
				}
			}
		}
	}
}

@media screen and (min-width: 3000px) {
	.overview {
		.body {
			height: calc(100% - 148px);
			.classify-box {
				padding-left: 11%;
			}
			.classify {
				width: 32%;
				&:nth-of-type(2n) {
					justify-content: flex-start;
				}
			}
		}
	}
}
</style>



